import s from './NewItems.module.css';
import {useNavigate} from "react-router-dom";
import {useEffect} from "react";

const NewItems = ({isLoggedIn}) => {
    const navigate = useNavigate();

    useEffect(()=>{
        if (!isLoggedIn){
            navigate('/login');
        }
    },[])

    return (<></>)
}

export default NewItems;